.progress-rounded {
  width: 270px;
  height: 270px;
  line-height: 50px;
  background: none;
  margin: 0 auto;
  box-shadow: none;
  position: absolute;
  z-index: 0;
}
 
.progress-rounded:after {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  // border: 56px solid #e4eaff;
  position: absolute;
  top: 0;
  left: 0;
}
 
.progress-rounded>span {
  width: 50%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  top: 0;
  z-index: 1;
}
 
.progress-rounded .progress-left {
  left: 0;
}
 
.progress-rounded .progress-bar {
  width: 100%;
  height: 100%;
  background: none;
  border-width: 36px;
  border-style: solid;
  position: absolute;
  top: 0;
}
 
.progress-rounded .progress-left .progress-bar {
  left: 100%;
  border-top-right-radius: 150px;
  border-bottom-right-radius: 150px;
  border-left: 0;
  transform-origin: center left;
  border-color: #181818;
}
 
.progress-rounded .progress-right {
  right: 0;
}
 
.progress-rounded .progress-right .progress-bar {
  left: -100%;
  border-top-left-radius: 150px;
  border-bottom-left-radius: 150px;
  border-right: 0;
  -webkit-transform-origin: center right;
  transform-origin: center right;
  animation: loading-1 1.8s linear forwards;
  border-color: #144945;
}
 
.progress-rounded .progress-value {
  width: 90%;
  height: 90%;
  border-radius: 50%;
  // background: #ffffff;
  font-size: 14px;
  color: #4c4c4c;
  line-height: 47px;
  text-align: center;
  position: absolute;
  top: 5%;
  left: 5%;
}
